//Home button

import { NavLink } from 'react-router-dom'
import React from 'react'
import styled from 'styled-components'

import HomeBtn from './HomeBtn.js';

const Power = styled.button`
position: fixed;
top: 2rem;
left: 50%;
transform: translate(-50%, 0);

background-color: #FCF6F4;
padding: 0.3rem;
border-radius: 50%;
border: 1px solid #000;
width: 2.5rem;
height: 2.5rem;

display: flex;
justify-content: center;
align-items: center;
z-index: 3;

cursor: pointer;

&:hover {
    background-color: rgba(109,163,77);
    box-shadow: 0 0 8px 6px rgba(109,163,77, 0.4);
}
`
const HomeButton = () => {
    return (
        <Power>
            <NavLink to="/">
          <HomeBtn width={30} height={30} fill='currentColor'/>
        </NavLink>
        </Power>
    )
}

export default HomeButton;